import React from 'react'
// components
import Cell from './cell'
// styles
import './grid.css'

const Grid = () => (
  <div className='grid'>
    <Cell />
    <Cell />
    <Cell />
    <Cell />
    <Cell />
    <Cell />
    <Cell />
    <Cell />
    <Cell />
  </div>
)

export default Grid



